.conteneur {
  font-family: sans-serif;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: grid;
  grid-template-columns: repeat(1, 200px);
  grid-gap: 20px;
  padding-top: 20px;
  padding-bottom: 25px;
}
.conteneur .box {
  width: 100%;
  height: 100%;
}
.conteneur .box h2 {
  display: block;
  text-align: center;
  color: rgb(3, 3, 3);
}
.conteneur .box .chart {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 30px;
  line-height: 140px;
  height: 140px;
  color: rgb(10, 10, 10);
}
.conteneur .box canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
@media (min-width: 420px) and (max-width: 659px) {
.conteneur {
    grid-template-columns: repeat(2, 200px);
    grid-gap: 60px;
}
}
@media (min-width: 660px) and (max-width: 899px) {
.conteneur {
    grid-template-columns: repeat(3, 200px);
    grid-gap: 70px;
}
}
@media (min-width: 900px) {
.conteneur {
    grid-template-columns: repeat(4, 200px);
    grid-gap: 80px;
}
}

